class FilmSF {
    constructor(filmDataSF) {
        this.data = filmDataSF;
    }
    isNotForAdult() {
        return this.data.adult;
    }
    #getCountry() {
        return this.data.countries
            .map(g => g.name)
            .join(", ");
    }

    renderFilmTableItemSF() {
        return `
        <tr>
            <td class="head_info">Страна</td>
            <td class="head_desc " id="sf-country">${this.#getCountry()}</td>
        </tr>
        <tr>
            <td class="head_info">Режиссер</td>
            <td class="head_desc">Михаэль Ноер</td>
        </tr>
        <tr>
            <td class="head_info">Сценарий</td>
            <td class="head_desc">Джеспер Финк, Михаэль Ноер</td>
        </tr>
        <tr>
            <td class="head_info">Продюсер</td>
            <td class="head_desc">Матильда Аппелин, Рене Эзра, Томас Радур, ...</td>
        </tr>
        <tr>
            <td class="head_info">Оператор</td>
            <td class="head_desc">Стурла Брандт Грёвлен</td>
        </tr>
        <tr>
            <td class="head_info">Композитор</td>
            <td class="head_desc">Руне Тонсгор Сёренсен</td>
        </tr>
        <tr>
            <td class="head_info">Художник</td>
            <td class="head_desc">Сорен Шварцберг, Луиз Ниссен</td>
        </tr>
        <tr class="head_desc__bold_block">
            <td class="head_info">Время</td>
            <td class="head_info head_desc__bold"> 104 мин. / 01:44</td>
        </tr>
        `
    }
}